import React from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import Feature1 from '../../assets/images/features/feature_1.png';
import Feature2 from '../../assets/images/features/feature_2.png';
import Feature3 from '../../assets/images/features/feature_3.png';
import Logo1 from '../../assets/images/logos/logo_1.png';
import Logo2 from '../../assets/images/logos/logo_2.png';
import Logo3 from '../../assets/images/logos/logo_3.png';
import Logo4 from '../../assets/images/logos/logo_4.png';
import Logo5 from '../../assets/images/logos/logo_5.png';

const FeaturesSection = styled.section`
    padding: 20px 0;
    background: ${({ theme }) => theme.colors.background};
    overflow: hidden;
`;

const Container = styled.div`
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 24px;
`;

const Grid = styled.div`
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 80px;
    margin: 80px 0 120px;
    padding-top: 80px;

    @media (max-width: 1024px) {
        grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 640px) {
        grid-template-columns: 1fr;
    }
`;

const IllustrationWrapper = styled.div`
    width: 280px;
    height: 280px;
    margin: -120px auto 32px;
    position: relative;
    transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    
    img {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
`;

const FeatureTitle = styled.h3`
    font-size: 32px;
    font-weight: 700;
    color: ${({ theme }) => theme.colors.textPrimary};
    margin-bottom: 16px;
    transition: color 0.3s ease;
`;

const FeatureDescription = styled.p`
    font-size: 18px;
    line-height: 1.6;
    color: ${({ theme }) => theme.colors.textSecondary};
    margin-bottom: 24px;
    opacity: 0.8;
`;

const LearnMoreLink = styled(Link)`
    display: inline-flex;
    align-items: center;
    color: ${({ theme }) => theme.colors.primary};
    font-weight: 500;
    text-decoration: none;
    font-size: 16px;
    transition: color 0.3s ease;
    
    &:after {
        content: '→';
        margin-left: 8px;
        transition: transform 0.3s ease;
    }
    
    &:hover:after {
        transform: translateX(4px);
    }
`;

const FeatureCard = styled(motion.div)`
    background: white;
    border-radius: 24px;
    padding: 40px;
    text-align: center;
    box-shadow: 0 4px 24px rgba(0, 0, 0, 0.05);
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    position: relative;
    z-index: 1;

    &:hover {
        transform: translateY(-12px);
        box-shadow: 0 12px 48px rgba(0, 0, 0, 0.12);

        ${IllustrationWrapper} {
            transform: translateY(-8px) scale(1.05);
        }

        ${FeatureTitle} {
            color: ${({ theme }) => theme.colors.primary};
        }

        ${LearnMoreLink} {
            color: ${({ theme }) => theme.colors.primaryDark};
            &:after {
                transform: translateX(8px);
            }
        }
    }
`;

const BrandsSection = styled.div`
    text-align: center;
`;

const BrandsTitle = styled.h2`
    font-size: 24px;
    font-weight: 500;
    color: ${({ theme }) => theme.colors.textPrimary};
    margin-bottom: 60px;
`;

const BrandsGrid = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 40px;
    flex-wrap: wrap;

    @media (max-width: 768px) {
        justify-content: center;
    }
`;

const BrandLogo = styled.img`
    height: 80px;
    opacity: 0.8;
    transition: opacity 0.2s ease;

    &:hover {
        opacity: 1;
    }
`;

const features = [
    {
        illustration: Feature1,
        title: "Development",
        description: "Web & app development",
        link: "/services/development"
    },
    {
        illustration: Feature2,
        title: "Design",
        description: "Web & graphics design",
        link: "/services/design"
    },
    {
        illustration: Feature3,
        title: "Support",
        description: "Proactive maintenance",
        link: "/services/support"
    }
];

const brands = [
    { logo: Logo1, name: "Agmino" },
    { logo: Logo2, name: "Alumo" },
    { logo: Logo3, name: "Cicular" },
    { logo: Logo4, name: "Helixa" },
    { logo: Logo5, name: "Quonex" }
];

const Features: React.FC = () => {
    return (
        <FeaturesSection>
            <Container>
                <Grid>
                    {features.map((feature, index) => (
                        <FeatureCard
                            key={index}
                            initial={{ opacity: 0, y: 20 }}
                            whileInView={{ opacity: 1, y: 0 }}
                            viewport={{ once: true }}
                            transition={{ delay: index * 0.2 }}
                        >
                            <IllustrationWrapper>
                                <img src={feature.illustration} alt={feature.title} />
                            </IllustrationWrapper>
                            <FeatureTitle>{feature.title}</FeatureTitle>
                            <FeatureDescription>{feature.description}</FeatureDescription>
                            <LearnMoreLink to={feature.link}>
                                Learn more
                            </LearnMoreLink>
                        </FeatureCard>
                    ))}
                </Grid>
                <BrandsSection>
                    <BrandsTitle>Some of the brands we work with...</BrandsTitle>
                    <BrandsGrid>
                        {brands.map((brand, index) => (
                            <BrandLogo
                                key={index}
                                src={brand.logo}
                                alt={brand.name}
                                initial={{ opacity: 0 }}
                                whileInView={{ opacity: 0.8 }}
                                viewport={{ once: true }}
                                transition={{ delay: index * 0.1 }}
                                as={motion.img}
                            />
                        ))}
                    </BrandsGrid>
                </BrandsSection>
            </Container>
        </FeaturesSection>
    );
};

export default Features;