// src/App.tsx
import React from 'react';
import { BrowserRouter } from 'react-router-dom';
import { ThemeProvider } from 'styled-components';
import theme from './styles/theme'; // 테마 임포트
import GlobalStyle from './styles/GlobalStyles'; // 전역 스타일 임포트
import Header from './components/layout/Header'; // 레이아웃 컴포넌트 임포트
import Footer from './components/layout/Footer';
import LandingPage from './pages/LandingPage'; // 페이지 컴포넌트 임포트

// 컴포넌트 정의
const App: React.FC = () => {
  return (
    <BrowserRouter>
      <ThemeProvider theme={theme}>
        <GlobalStyle /> {/* 전역 스타일 적용 */}
        <Header />
        <main> {/* 시맨틱 태그 사용 */}
          <LandingPage />
        </main>
        <Footer />
      </ThemeProvider>
    </BrowserRouter>
  );
}

export default App;